<template>

  <div >
    <div id="titol">
      <h2> <br> MUNICIÓ </h2>
    </div>

    <p><strong>Items: 99</strong></p>

    <div id="Main">
        <div class="MunicioCard">
          <b-card-group columns> 
            <div columns v-for="proyecto in municio" :key="proyecto.id">
              <CartaJson :srcimg="proyecto.srcimg" :alt="proyecto.alt" :titulo="proyecto.titulo" :texto="proyecto.texto">
              </CartaJson> 
            </div>
          </b-card-group>
        </div>

        <div id="ContadorPagines">
          <b-pagination-nav :link-gen="linkGen" :number-of-pages="11" use-router ></b-pagination-nav>
        </div>
      </div>

    <FooterPrimer />
  </div>
</template>

<script>
  import FooterPrimer from '@/components/FooterPrimer.vue'
  import CartaJson from '../components/CartaJson.vue'
  const lstMunicioJson='../Municio.json'

  export default {
    name: 'MunicioView',
    props:{
      name:{
        type:String,
        default: 'LlistaMunicio'
      }
    },
    components: {
    
    FooterPrimer,
    CartaJson
  },
  data(){
    return{
      municio:[]
    }
  },
  mounted (){
    this.fetchData()
  },

  methods: {
      linkGen(pageNum) {
        return pageNum === 1 ? '?' : `?page=${pageNum}`
      },
      async fetchData(){
        const res= await fetch(lstMunicioJson);
        const val= await res.json();
        this.municio=val;
        console.log(val);
      }
    }
  }
</script>

<style scoped>
  h2{
    text-align: center;
    height: 7vw;
    color: white;
  }

  #titol{
    background-color: rgb(58, 58, 60);
    margin-bottom: 4vw;
  }

  p{
    margin-left: 5%;
  }

  #Main{
    margin: auto;
    width: 70%;
  }

  #ContadorPagines{
   margin: auto;
  }
</style>